* {
  font-family: "Gill Sans", "Gill Sans MT", Calibri, "Trebuchet MS", sans-serif;
}
.mapContainer,
.mapContainer * {
  padding: 12 !important;
  margin: 0 !important;
  border-radius: 0 !important;
  border-color: none !important;
  border: none !important;
  color: #000;
}
a:hover {
  transform: scale(1.1); /* Cambio de tamaño al hacer hover */
  background: linear-gradient(180deg, #ba001300, #ff3246, #ba001300);
}
a:active {
  /* Aquí puedes agregar los estilos que quieras para cuando se hace clic */
  transform: scale(0.95);
}
button:hover {
  transform: scale(1.1); /* Cambio de tamaño al hacer hover */
}
p {
  font-size: 17px;
}
