  font-size: 18px;
  padding: 5%;
  border-radius: 50px;
}

h2 {
  font-size: 30px; /* Títulos más grandes */
  text-align: center;
  background: linear-gradient(90deg, #ba001300, #ff3246, #ba001300);
  color: #ffffff;
}
button:active {
  /* Aquí puedes agregar los estilos que quieras para cuando se hace clic */
  transform: scale(0.95);
}
/* LandingPage.css */

body {
  /* Define el color inicial de fondo */
  background-color: #1a61b3; /* Color inicial */
  background: linear-gradient(#1a61b3, rgb(54, 54, 216), #1a61b3);

  /* Define el fondo con un gradiente que se vuelve más claro a medida que se desplaza hacia abajo */
  background-size: 100% 100vh; /* Ajusta el tamaño del gradiente */
  background-attachment: fixed; /* Mantiene el gradiente fijo al hacer scroll */
  background-repeat: no-repeat;
}

a:-webkit-any-link {
  text-decoration: none;
  color: aliceblue;
}
.infoContent {
  backdrop-filter: blur(0.4rem);
  -webkit-backdrop-filter: blur(0.4rem);
  box-shadow: 0 20px 32px 0 black;
  padding: 20px;
  text-align: center;
  max-width: 250px;
  max-height: 250px;
}

.icon {
  box-shadow: 0 20px 32px 0 black;
}
.swal2-title {
  background: linear-gradient(180deg, #ba001300, #ba001300);
  color: white;
}
div:where(.swal2-container) .swal2-html-container {
  color: white;
}
select {
  width: 120px;
  padding: 10px; /* Aumento del relleno */
  border-radius: 25px; /* Reducción del radio del borde */
  border: 5px solid #00000028;
  color: #000;
}
option {
  width: 120px;
  padding: 10px; /* Aumento del relleno */
  border-radius: 25px; /* Reducción del radio del borde */
  border: 5px solid #00000028;
  color: #000;
}
.swal2-popup.swal2-modal.swal2-show {
  background: #fafafa10;
  background-size: cover;
  backdrop-filter: blur(0.4rem);
  -webkit-backdrop-filter: blur(0.4rem);
  border: 3px solid white;
  border-radius: 20px;
  box-shadow: 0 20px 32px 0 black;
  color: white;
}

.Toastify__toast.Toastify__toast-theme--light.Toastify__toast--error.Toastify--animate.Toastify__bounce-enter--top-right {
  box-shadow: 0 20px 32px 0 black;

  border: 3px solid white;
  border-radius: 20px;
  top: 5vh; /* Ajusta según sea necesario */
  right: 1vh; /* Ajusta según sea necesario */
  position: fixed; /* Añade esto para hacer que el botón del menú sea flotante */
}
.Toastify__toast.Toastify__toast-theme--light.Toastify__toast--success.Toastify--animate.Toastify__bounce-enter--top-right {
  box-shadow: 0 20px 32px 0 black;

  border: 3px solid white;
  border-radius: 20px;
  top: 5vh; /* Ajusta según sea necesario */
  right: 1vh; /* Ajusta según sea necesario */
  position: fixed; /* Añade esto para hacer que el botón del menú sea flotante */
}

.instrucciones {
  margin-right: 30px;
  max-height: 50vh;
  background: #ffffff09;
  background-size: cover;
  backdrop-filter: blur(0.4rem);
  -webkit-backdrop-filter: blur(0.4rem);
  box-shadow: 0 20px 32px 0 black;
overflow-y: auto;
  border: 3px solid #fafafa10;
  border-radius: 50px;
  padding: 7%;
}
.instrucciones::-webkit-scrollbar {
  width: 7px; /* Ancho de la barra de desplazamiento */
  height: 7px;
}

.instrucciones::-webkit-scrollbar-track {
  background: transparent; /* Color de la pista de la barra de desplazamiento */
}

.instrucciones::-webkit-scrollbar-thumb {
  background-color: rgba(
    255,
    0,
    0,
    0.24
  ); /* Color del pulgar de la barra de desplazamiento */
  border-radius: 50px; /* Radio del borde del pulgar de la barra de desplazamiento */
  border: 1px solid black; /* Borde del pulgar de la barra de desplazamiento */
}
.modalContent {
  overflow-y: auto;
  max-height: 80vh;
}
.modalContent::-webkit-scrollbar {
  width: 7px; /* Ancho de la barra de desplazamiento */
  height: 7px;
}

.modalContent::-webkit-scrollbar-track {
  background: transparent; /* Color de la pista de la barra de desplazamiento */
}

.modalContent::-webkit-scrollbar-thumb {
  background-color: rgba(
    255,
    0,
    0,
    0.24
  ); /* Color del pulgar de la barra de desplazamiento */
  border-radius: 50px; /* Radio del borde del pulgar de la barra de desplazamiento */
  border: 1px solid black; /* Borde del pulgar de la barra de desplazamiento */
}
.swal2-success-circular-line-left {
  background-color: rgba(
    255,
    0,
    0,
    0.24
  ); /* Color del pulgar de la barra de desplazamiento */
}

.requerido {
  color: rgba(249, 249, 36, 0.85);
}
.Toastify__close-button.Toastify__close-button--light {
  background: transparent;
}
.swal2-popup.swal2-modal.swal2-hide {
  background: #fafafa10;
  background-size: cover;
  backdrop-filter: blur(0.4rem);
  -webkit-backdrop-filter: blur(0.4rem);
  box-shadow: 0 20px 32px 0 black;
  border: 3px solid white;
  border-radius: 50px;
  box-shadow: 0 20px 32px 0 black;
}

.my-button {
  position: fixed;
  right: -50px; /* Oculta inicialmente el botón a la derecha */
  top: 50%;
  z-index: 9999;
  padding: 0;
  border: none;
  background: none;
  transition: right 1s; /* Agrega una transición suave */
  box-shadow: 0 20px 32px 0 black;
}

.my-button:hover {
  right: 2%; /* Muestra el botón cuando el mouse pasa por encima */
}

.imgbutton {
  width: 150px;
  height: auto;
  border-radius: 50px;
}

html {
  scrollbar-width: thin;
  scrollbar-color: rgba(155, 155, 155, 0.5) transparent;
  background-color: #034c8b;
}

html::-webkit-scrollbar {
  width: 7px; /* Ancho de la barra de desplazamiento */
  height: 7px;
  border-radius: 50px;
  z-index: 99999999;
}

html::-webkit-scrollbar-track {
  background: transparent;
  z-index: 99999999;
}

html::-webkit-scrollbar-thumb {
  z-index: 99999999;
  background-color: red;
  border-radius: 50px;
  border: 2px solid black;
}
/* Estilos para el modo claro */
.light-mode {
  background-color: #034c8b;
  background: linear-gradient(#034c8b, #2477c0, #034c8b);
  background-size: 100% 100vh; /* Ajusta el tamaño del gradiente */
  background-attachment: fixed; /* Mantiene el gradiente fijo al hacer scroll */
  background-repeat: no-repeat;
  text-shadow: 1px 1px 5px black;
}
/* Estilos para el modo oscuro */
.dark-mode {
  background-color: #010326;
  background: linear-gradient(#010326, #0a1a74, #010326);
  text-shadow: 1px 1px 3px black;
  background-size: 100% 100vh; /* Ajusta el tamaño del gradiente */
  background-attachment: fixed; /* Mantiene el gradiente fijo al hacer scroll */
  background-repeat: no-repeat;
}

.light-mode a:hover {
  transform: scale(1.1); /* Cambio de tamaño al hacer hover */
  background: linear-gradient(180deg, #ba001300, #ff3246, #ba001300);
}

.light-mode button {
  background-color: #ff3246;
}
.dark-mode tr:hover {
  background: #8c030377;
  background-size: cover;
  backdrop-filter: blur(0.4rem);
  -webkit-backdrop-filter: blur(0.4rem);
  box-shadow: 0 8px 20px 0 rgba(255, 0, 0, 0.445);
}
.light-mode tr:hover {
  background: #ff324676;
  background-size: cover;
  backdrop-filter: blur(0.4rem);
  -webkit-backdrop-filter: blur(0.4rem);
  box-shadow: 0 8px 20px 0 rgba(255, 0, 0, 0.445);
}
.light-mode h2 {
  background: linear-gradient(90deg, #ba001300, #ff3246, #ba001300);
}

.light-mode html::-webkit-scrollbar-track {
  background: transparent;
  z-index: 99999999;
}
.light-mode html {
  background-color: #1a61b3;
}

.dark-mode html::-webkit-scrollbar-track {
  background: transparent;
  z-index: 99999999;
}

.dark-mode a:hover {
  transform: scale(1.1); /* Cambio de tamaño al hacer hover */
  background: linear-gradient(180deg, #ba001300, #8c0303, #ba001300);
}

.dark-mode button {
  background-color: #8c0303;
}

.dark-mode h2 {
  background: linear-gradient(90deg, #ba001300, #8c0303, #ba001300);
}

.dark-mode a:-webkit-any-link {
  text-decoration: none;
}
.dark-mode html {
  background-color: #010326;
}

/* Para pantallas pequeñas */
@media (max-width: 600px) {
  .my-button {
    right: -25px; /* Oculta parcialmente el botón a la derecha */
    width: 80px; /* Ajusta el tamaño del botón */
  }

  .imgbutton {
    width: 80px;
  }
  body {
    font-size: 14px; /* Ajusta el tamaño de la fuente */
  }
  .swal2-popup.swal2-modal.swal2-show {
    left: 0;
  }

  .swal2-popup.swal2-modal.swal2-hide {
    left: 0%;
  }
  /* Ajusta otros estilos según sea necesario */
}

/* Para pantallas medianas */
@media (min-width: 601px) and (max-width: 1024px) {
  .my-button {
    right: -25px; /* Oculta parcialmente el botón a la derecha */
    width: 80px; /* Ajusta el tamaño del botón */
  }
  .imgbutton {
    width: 80px;
  }
  body {
    font-size: 16px; /* Ajusta el tamaño de la fuente */
  }

  /* Ajusta otros estilos según sea necesario */
}

/* Para pantallas grandes */
@media (min-width: 1025px) {
  .my-button {
    right: -50px; /* Oculta parcialmente el botón a la derecha */
    width: 150px; /* Ajusta el tamaño del botón */
  }

  body {
    font-size: 18px; /* Ajusta el tamaño de la fuente */
  }

  /* Ajusta otros estilos según sea necesario */
}
