
.PuntosInteres {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 2em;
    margin: 7%;
    margin-top: 2%;
    margin-bottom: 2%;
    background: #010e1f68;
    background-size: cover;
    backdrop-filter: blur(0.4rem);
    -webkit-backdrop-filter: blur(0.4rem);
    box-shadow: 0 20px 32px 0 black;
  
    border: 3px solid #fafafa10;
    border-radius: 50px;
    padding: 7%;
  }
  
  .tablaPuntosInteres table {
    width: 100%;
    border-collapse: collapse; /* Elimina el espacio entre las celdas */
    border-radius: 50px 50px 25px 25px;
  }
  
  .PuntosInteres th,
  .PuntosInteres td {
    padding: 10px;
    text-align: left;
    border-bottom: 1px solid #ddd; /* Añade una línea debajo de cada fila */
  }
  /* Estilos para los botones de acciones */
  .tablaPuntosInteres button {
    padding: 10px;
    font-size: 15px;
  }
  
  
  .PuntosInteres p {
    text-align: justify; /* Justifica el texto */
  }
  
  /* Añade un efecto hover a las filas */
  .PuntosInteres tr:hover {
    background: #fafafa10;
    background-size: cover;
    backdrop-filter: blur(0.4rem);
    box-shadow: 0 8px 20px 0 rgba(255, 0, 0, 0.445);
  
    -webkit-backdrop-filter: blur(0.4rem);
  }
  
  /* Añade estilos a la entrada de texto del filtro */
  .PuntosInteres input {
    width: 100%;
    padding: 10px; /* Aumento del relleno */
    border-radius: 25px; /* Reducción del radio del borde */
    border: 5px solid #00000028;
    color: #000;
  }
  
  .PuntosInteres label {
    display: block;
    margin-bottom: 8px;
    font-weight: bold;
  }
  
  .tablaPuntosInteres {
    overflow-x: auto; /* Permite el desplazamiento horizontal si la tabla es más ancha que la pantalla */
    width: 100%; /* Asegura que la tabla ocupe todo el ancho disponible */
    max-height: 100vh; /* Limita la altura de la tabla a la mitad de la altura de la vista */
    border-radius: 30px;
  }
  
  .tablaPuntosInteres::-webkit-scrollbar {
    width: 5px; /* Ancho de la barra de desplazamiento */
    height: 5px; /* Ancho de la barra de desplazamiento */
    border-radius: 50px; /* Radio del borde del pulgar de la barra de desplazamiento */
    border: 1px solid black; /* Borde del pulgar de la barra de desplazamiento */
  
  }
  
  .tablaPuntosInteres::-webkit-scrollbar-track {
    background: #1a31b3; /* Color de la pista de la barra de desplazamiento */
  }
  
  .tablaPuntosInteres::-webkit-scrollbar-thumb {
    background-color: red; /* Color del pulgar de la barra de desplazamiento */
    border-radius: 50px; /* Radio del borde del pulgar de la barra de desplazamiento */
    border: 1px solid black; /* Borde del pulgar de la barra de desplazamiento */
  }
  .light-mode .tablaPuntosInteres button {
    background-color: transparent;
    border: none;
  }
  .light-mode .tablaPuntosInteres button:hover {
    background: linear-gradient(90deg, #ba001300, #ff3246, #ba001300);
    border: none;
  }
  .dark-mode .tablaPuntosInteres button:hover {
    background: linear-gradient(90deg, #ba001300, #8C0303, #ba001300);
    border: none;
  }
  .dark-mode .tablaPuntosInteres button {
    background-color: transparent;
    border: none;
  }
  /* Estilos para pantallas grandes */
  @media (min-width: 1200px) {
    .PuntosInteres {
      padding: 20px;
    }
  
    .PuntosInteres h2 {
      font-size: 2em;
    }
  
    .PuntosInteres table {
      font-size: 1em;
    }
  }
  
  /* Estilos para pantallas medianas */
  @media (min-width: 768px) and (max-width: 1199px) {
    .PuntosInteres {
      padding: 15px;
    }
  
    .PuntosInteres h2 {
      font-size: 1.75em;
    }
  
    .PuntosInteres table {
      font-size: 0.875em;
      align-self: first baseline;
    }
  }
  
  .modalContent {
    position: absolute;
    top: 50%;
    left: 57.7%;
    right: auto;
    bottom: auto;
    margin-right: -70%;
    transform: translate(-50%, -50%);
    background: #fafafa10;
    background-size: cover;
    backdrop-filter: blur(0.4rem);
    -webkit-backdrop-filter: blur(0.4rem);
    border: 3px solid fafafa10;
    border-radius: 50px;
    padding: 30px; /* Relleno de 30px */
    width: 40%; /* Ancho de 600px */
    box-shadow: 0 20px 32px 0 black;
  
  }
  
  /* Estilos para el fondo del modal */
  .modalOverlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.75); /* Fondo oscuro */
  }
  
  .modalInput {
    width: 100%;
    padding: 15px; /* Aumento del relleno */
    border-radius: 25px; /* Reducción del radio del borde */
    border: 5px solid #00000028;
    font-size: 17px;
    color: white;
    margin: 10px; /* Margen de 10px */
  }
  
  
  
  /* Estilos para el contenedor de los botones */
  .btnContainer {
    display: flex;
    justify-content: center;
    gap: 1%;
  }
  
  
  /* Estilos para el botón de agregar */
  .agregarBtn {
    background-color: #4CAF50; /* Verde */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 5%; /* Margen de 20px */
    cursor: pointer;
  }
  
  /* Estilos para el botón de cancelar */
  .cancelarBtn {
    background-color: #f44336; /* Rojo */
    border: none;
    color: white;
    padding: 15px 32px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 5%; /* Margen de 20px */
  
    cursor: pointer;
  }
  
  
  /* Estilos para pantallas pequeñas */
  @media (max-width: 767px) {
    .PuntosInteres {
      padding: 10px;
      align-items: center;
    }
  
    .PuntosInteres h2 {
      font-size: 1.5em;
    }
  
    .PuntosInteres table {
      font-size: 0.75em;
      align-self: first baseline;
    }
    .PuntosInteres table, .PuntosInteres th, .PuntosInteres td, .PuntosInteres {
      min-width: 100px;
    }
    .modalContent {
      left: 50%;
      padding: 20px; /* Relleno de 30px */
      width: 80%; /* Ancho de 600px */
    }
    
  }
  